.timer{display:flex;align-items:center;gap:.5rem;margin-bottom:1rem}.timer span{padding:1rem .75rem;background:rgba(var(--foreground-rgb), 0.15);border-radius:.5rem;font-size:var(--subheadline-size);font-weight:var(--subheadline-weight);letter-spacing:var(--subheadline-spacing);line-height:var(--subheadline-height)}.timer span::after{content:"";position:absolute;display:block;top:0;left:0;width:100%;height:50%;background:rgba(var(--black-rgb), 0.2)}